import classes from './MealItemForm.module.css';
import Input from '../../UI/Input';
import { useRef, useState } from 'react';

const MealItemForm = (props) => {
  const [amountIsValid, setAmountIsValid] = useState(true);
  const amountInputRef = useRef();
  const submitHandler = (event) =>{
    event.preventDefault();
    const enteredAmount = amountInputRef.current.value;
    const enteredAmountNumber = +enteredAmount; //siempre en los ref llegan strings, el + lo vuelve numero
    if(enteredAmount.trim().length === 0 || enteredAmountNumber<1 || enteredAmountNumber>5){//le quita los espacios en blanco al string y mira si está vacío
      setAmountIsValid(false);
      return;
    }
    props.onAddToCart(enteredAmountNumber);
  }

  return (
    <form className={classes.form} onSubmit={submitHandler}>
        <Input label="Amount" ref={amountInputRef} input={{
            id: 'amount',
            type: 'number',
            min: '1',
            max: '5',
            step: '1',
            defaultValue: '1'
        }} />
        <button>+ Add</button>
        { !amountIsValid && <p>Plase enter a valid amount</p>}
    </form>
  );
};

export default MealItemForm;